<template>
    <section>
        <!--工具条-->
        <toolbar
            :buttonList="buttonList"
            @callFunction="callFunction"
        ></toolbar>

        <!--列表-->
        <el-table
            :data="Tasks"
            :row-class-name="$tableRowClassName"
            v-loading="listLoading"
            style="width: 100%"
        >
            <el-table-column type="index" width="80"></el-table-column>
            <el-table-column
                prop="Key"
                label="钥匙"
                width
                sortable
            ></el-table-column>
            <el-table-column
                prop="Secret"
                label="秘钥"
                width
                sortable
            ></el-table-column>

            <el-table-column prop="Name" label="供应商名称" width="" sortable>
            </el-table-column>

            <el-table-column
                prop="Apis"
                label="可访问接口"
                width
                sortable
            ></el-table-column>
            <el-table-column
                label="操作"
                width="150"
                v-if="canEdit || canDelete"
            >
                <template slot-scope="scope">
                    <el-button
                        v-if="canEdit"
                        plain
                        type="warning"
                        size="small"
                        @click="handleEdit(scope.$index, scope.row)"
                        >编辑</el-button
                    >
                    <el-button
                        v-if="canDelete"
                        plain
                        type="danger"
                        size="small"
                        @click="handleDel(scope.$index, scope.row)"
                        >删除</el-button
                    >
                </template>
            </el-table-column>
        </el-table>

        <!--工具条-->
        <el-col :span="24" class="toolbar">
            <el-pagination
                layout="total, prev, pager, next, jumper"
                @current-change="handleCurrentChange"
                :page-size="pagesize"
                :current-page="page"
                :total="total"
                style="float: right"
            ></el-pagination>
        </el-col>

        <!--编辑界面-->
        <el-dialog
            title="编辑"
            :visible.sync="editFormVisible"
            v-model="editFormVisible"
            :close-on-click-modal="false"
        >
            <el-form
                :model="editForm"
                label-width="100px"
                :rules="editFormRules"
                ref="editForm"
            >
                <el-form-item label="钥匙" prop="Key">
                    <el-input
                        v-model="editForm.Key"
                        auto-complete="off"
                    ></el-input>
                </el-form-item>
                <el-form-item label="秘钥" prop="Secret">
                    <el-input
                        v-model="editForm.Secret"
                        auto-complete="off"
                    ></el-input>
                </el-form-item>
                <el-form-item label="供应商名称" prop="Name">
                    <el-input
                        v-model="editForm.Name"
                        auto-complete="off"
                    ></el-input>
                </el-form-item>
                <el-form-item label="可访问接口" prop="Apis">
                    <el-input
                        v-model="editForm.Apis"
                        auto-complete="off"
                    ></el-input>
                </el-form-item>
                <el-form-item label="用户ID" prop="Userid">
                    <el-input
                        v-model="editForm.Userid"
                        auto-complete="off"
                    ></el-input>
                </el-form-item>
                <el-form-item label="分组ID" prop="Groupid">
                    <el-input
                        v-model="editForm.Groupid"
                        auto-complete="off"
                    ></el-input>
                </el-form-item>
                <el-form-item label="可访问IP" prop="Ips">
                    <el-input
                        v-model="editForm.Ips"
                        auto-complete="off"
                    ></el-input>
                </el-form-item>
                <el-form-item label="服务器域名" prop="Domains">
                    <el-input
                        v-model="editForm.Domains"
                        auto-complete="off"
                    ></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="editFormVisible = false"
                    >取消</el-button
                >
                <el-button
                    type="primary"
                    @click.native="editSubmit"
                    :loading="editLoading"
                    >提交</el-button
                >
            </div>
        </el-dialog>

        <!--新增界面-->
        <el-dialog
            title="新增"
            :visible.sync="addFormVisible"
            v-model="addFormVisible"
            :close-on-click-modal="false"
        >
            <el-form
                :model="addForm"
                label-width="100px"
                :rules="addFormRules"
                ref="addForm"
            >
                <el-form-item label="钥匙串" prop="Key">
                    <el-input
                        v-model="addForm.Key"
                        auto-complete="off"
                    ></el-input>
                </el-form-item>
                <el-form-item label="秘钥" prop="Secret">
                    <el-input
                        v-model="addForm.Secret"
                        auto-complete="off"
                    ></el-input>
                </el-form-item>
                <el-form-item label="供应商名称" prop="Name">
                    <el-input
                        v-model="addForm.Name"
                        auto-complete="off"
                    ></el-input>
                </el-form-item>
                <el-form-item label="可访问接口" prop="Apis">
                    <el-input
                        v-model="addForm.Apis"
                        auto-complete="off"
                    ></el-input>
                </el-form-item>
                <el-form-item label="用户ID" prop="Userid">
                    <el-input
                        v-model="addForm.Userid"
                        auto-complete="off"
                    ></el-input>
                </el-form-item>
                <el-form-item label="分组ID" prop="Groupid">
                    <el-input
                        v-model="addForm.Groupid"
                        auto-complete="off"
                    ></el-input>
                </el-form-item>
                <el-form-item label="可访问IP" prop="Ips">
                    <el-input
                        v-model="addForm.Ips"
                        auto-complete="off"
                    ></el-input>
                </el-form-item>
                <el-form-item label="服务器域名" prop="Domains">
                    <el-input
                        v-model="addForm.Domains"
                        auto-complete="off"
                    ></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="addFormVisible = false"
                    >取消</el-button
                >
                <el-button
                    type="primary"
                    @click.native="addSubmit"
                    :loading="addLoading"
                    >提交</el-button
                >
            </div>
        </el-dialog>
    </section>
</template>

<script>
import util from "../../../util/date";
import {
    getKeySecretList,
    deleteKeySecret,
    editKeySecret,
    addKeySecret,
} from "../../api/api";
import { getButtonList } from "../../promissionRouter";
import Toolbar from "../../components/Toolbar";

export default {
    components: { Toolbar },
    data() {
        return {
            filters: {
                name: "",
            },
            buttonList: [],
            Tasks: [],
            total: 0,
            page: 1,
            pagesize: 10,
            listLoading: false,
            currentRow: null,
            addDialogFormVisible: false,
            editFormVisible: false, //编辑界面是否显示
            editLoading: false,
            editFormRules: {
                Key: [
                    {
                        required: true,
                        message: "请输入钥匙串",
                        trigger: "blur",
                    },
                ],
                Secret: [
                    { required: true, message: "请输入秘钥", trigger: "blur" },
                ],
                Name: [
                    {
                        required: true,
                        message: "请输入供应商名称",
                        trigger: "blur",
                    },
                ],
                Apis: [
                    {
                        required: true,
                        message: "请输入可访问接口",
                        trigger: "blur",
                    },
                ],
            },
            //编辑界面数据
            editForm: {
                Id: 0,
                Key: "",
                Secret: "",
                Name: "",
                Apis: "",
                Userid: "",
                Groupid: "",
                Ips: "",
                Domains: "",
            },

            addFormVisible: false, //新增界面是否显示
            addLoading: false,
            addFormRules: {
                Key: [
                    {
                        required: true,
                        message: "请输入钥匙串",
                        trigger: "blur",
                    },
                ],
                Secret: [
                    { required: true, message: "请输入秘钥", trigger: "blur" },
                ],
                Name: [
                    {
                        required: true,
                        message: "请输入供应商名称",
                        trigger: "blur",
                    },
                ],
                Apis: [
                    {
                        required: true,
                        message: "请输入可访问接口",
                        trigger: "blur",
                    },
                ],
            },
            //新增界面数据
            addForm: {
                Id: 0,
                Key: "",
                Secret: "",
                Name: "",
                Apis: "",
                Userid: "",
                Groupid: "",
                Ips: "",
                Domains: "",
            },
        };
    },
    methods: {
        selectCurrentRow(val) {
            this.currentRow = val;
        },
        callFunction(item) {
            this.filters = {
                name: item.search,
            };
            this[item.Func].apply(this, item);
        },
        handleCurrentChange(val) {
            this.page = val;
            this.getList();
        },
        getList() {
            let _this = this;
            let para = {
                page: this.page,
                size: this.pagesize,
                key: this.filters.name,
            };
            this.listLoading = true;
            getKeySecretList(para).then((res) => {
                if (res.success) {
                    this.total = res.data.dataCount;
                    this.Tasks = res.data.data;
                }
                this.listLoading = false;
            });
        },
        //删除
        handleDel(index, r) {
            let row = r;
            if (!row) {
                this.$message({
                    message: "请选择要编辑的一行数据！",
                    type: "error",
                });

                return;
            }
            this.$confirm("确认删除该记录吗?", "提示", {
                type: "warning",
            })
                .then(() => {
                    this.listLoading = true;
                    let para = { id: row.KSCID };
                    deleteKeySecret(para).then((res) => {
                        if (util.isEmt.format(res)) {
                            this.listLoading = false;
                            return;
                        }
                        this.listLoading = false;
                        if (res.success) {
                            this.$message({
                                message: "删除成功",
                                type: "success",
                            });
                        } else {
                            this.$message({
                                message: res.msg,
                                type: "error",
                            });
                        }

                        this.getList();
                    });
                })
                .catch(() => {});
        },
        //显示编辑界面
        handleEdit(index, r) {
            let row = r;
            if (!row) {
                this.$message({
                    message: "请选择要编辑的一行数据！",
                    type: "error",
                });

                return;
            }

            this.editFormVisible = true;
            this.editForm = Object.assign({}, row);
        },
        //显示新增界面
        handleAdd() {
            this.addFormVisible = true;
            this.addForm = {
                Id: 0,
                Key: "",
                Secret: "",
                Name: "",
                Apis: "",
                Userid: "",
                Groupid: "",
                Ips: "",
                Domains: "",
            };
        },
        //编辑
        editSubmit: function () {
            this.$refs.editForm.validate((valid) => {
                if (valid) {
                    //this.$confirm("确认提交吗？", "提示", {}).then(() => {
                    this.editLoading = true;
                    //NProgress.start();
                    let para = Object.assign({}, this.editForm);
                    editKeySecret(para).then((res) => {
                        if (util.isEmt.format(res)) {
                            this.editLoading = false;
                            return;
                        }

                        if (res.success) {
                            this.editLoading = false;
                            //NProgress.done();
                            this.$message({
                                message: "修改成功",
                                type: "success",
                            });
                            this.$refs["editForm"].resetFields();
                            this.editFormVisible = false;
                            this.getList();
                        } else {
                            this.$message({
                                message: res.msg,
                                type: "error",
                            });
                        }
                    });
                    //});
                }
            });
        },
        //新增
        addSubmit: function () {
            let _this = this;
            this.$refs.addForm.validate((valid) => {
                if (valid) {
                    //this.$confirm("确认提交吗？", "提示", {}).then(() => {
                    this.addLoading = true;
                    //NProgress.start();
                    let para = Object.assign({}, this.addForm);
                    addKeySecret(para).then((res) => {
                        if (util.isEmt.format(res)) {
                            this.addLoading = false;
                            return;
                        }
                        if (res.success) {
                            this.addLoading = false;
                            //NProgress.done();
                            this.$message({
                                message: "新增成功",
                                type: "success",
                            });
                            this.$refs["addForm"].resetFields();
                            this.addFormVisible = false;
                            this.getList();
                        } else {
                            this.$message({
                                message: res.msg,
                                type: "error",
                            });
                        }
                        this.addLoading = false;
                    });
                    //});
                }
            });
        },
    },
    mounted() {
        this.getList();
        let routers = window.sessionStorage.router
            ? JSON.parse(window.sessionStorage.router)
            : [];
        let buttons = getButtonList(this.$route.path, routers);
        buttons.forEach((element) => {
            //&& element.IsButton && !element.IsHide
            if (element.Func == "handleEdit") {
                this.canEdit = true;
            } else if (element.Func == "handleDel") {
                this.canDelete = true;
            } else {
                this.buttonList.push(element);
            }
        });
    },
};
</script>

<style scoped>
</style>



